@import url("https://fonts.googleapis.com/css2?family=Oleo+Script+Swash+Caps&family=Open+Sans:wght@300;400&family=Pacifico&display=swap");

:root {
  --primary-text-color: #171717;
  --orange: #e8a644;
  --off-white: #eaeaea;
  --grey-light: #bcbcbc;
  --grey-mild: #828282;
  --grey-strong: #606060;
  --secondary-text-color: #2b2b2b;
  --green: #378870;
  --fluro-green: #6dbd3a;
  --intro-font: "Pacifico", cursive;
  --body-font: "Open Sans", sans-serif;
  --h-font: "Oleo Script Swash Caps", cursive;
}
.portfolio-card-container {
  margin: 30px;
  background-color: var(--off-white);
  border-radius: 25px;
  border: 2px solid;
  border-color: var(--grey-strong);
  position: relative;
  overflow: hidden;
}

.portfolio-card-container h1 {
  font-size: 2.6rem;
  font-family: var(--h-font);
  color: var(--secondary-text-color);
  padding: 10px;
  background: rgba(14, 14, 15, 0.473);
  position: relative;
}
.portfolio-card-container h1:hover {
  animation: color 1s forwards;
}
@keyframes color {
  0% {
    color: var(--secondary-text-color);
  }
  26% {
    color: var(--orange);
  }
  77% {
    color: var(--green);
  }
  100% {
    color: var(--secondary-text-color);
  }
}

.portfolio-card-container h1:after {
  position: absolute;
  content: "";
  height: 7px;
  bottom: 10%;
  width: 50%;
  left: 0;
  background: var(--green);
  transition: 0.5s;
}

.portfolio-card-container h1:hover:after {
  width: 100%;
  background: var(--orange);
}
.portfolio-card-container p {
  font-size: 0.8rem;
  padding: 10px;
}

.icons-container {
  text-align: right;
  position: relative;
}

.icons-container ul,
.icons-container h3 {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}
.icons-container h3 {
  padding-right: 10px;
}

.project-img-wrapper {
  position: relative;
  height: 230px;
  overflow: hidden;
  width: 100%;
}

